import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
declare var Konva:any
/**
 * Generated class for the DesignCanvasPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-design-canvas',
  templateUrl: 'design-canvas.html',
})
export class DesignCanvasPage {
customDressDetails:any;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    this.customDressDetails = this.navParams.get('data');
    console.log(this.customDressDetails);
    // console.log('ionViewDidLoad DesignCanvasPage');
  }

  width = window.innerWidth;
  height = window.innerHeight;
  
  tween = null;
  
  addStar(layer, stage) {
    var scale = Math.random();
  
    var star = new Konva.Star({
      x: Math.random() * stage.getWidth(),
      y: Math.random() * stage.getHeight(),
      numPoints: 5,
      innerRadius: 30,
      outerRadius: 50,
      fill: '#89b717',
      opacity: 0.8,
      draggable: true,
      scale: {
        x : scale,
        y : scale
      },
      rotation: Math.random() * 180,
      shadowColor: 'black',
      shadowBlur: 10,
      shadowOffset: {
        x : 5,
        y : 5
      },
      shadowOpacity: 0.6,
      // custom attribute
      startScale: scale
    });
  
    layer.add(star);
  
  stage = new Konva.Stage({
    container: 'container',
    width: this.width,
    height: this.height
  });
  
   layer = new Konva.Layer();
   let dragLayer = new Konva.Layer();
  
  for(let n = 0; n < 30; n++) {
    this.addStar(layer, stage);
  }
  
  stage.add(layer, dragLayer);
  
  stage.on('dragstart', function(evt) {
    var shape = evt.target;
    // moving to another layer will improve dragging performance
    shape.moveTo(this.dragLayer);
    this.stage.draw();
    
    if (this.tween) {
      this.tween.pause();
    }
    shape.setAttrs({
      shadowOffset: {
        x: 15,
        y: 15
      },
      scale: {
        x: shape.getAttr('startScale') * 1.2,
        y: shape.getAttr('startScale') * 1.2
      }
    });
  });
  
  stage.on('dragend', function(evt) {
    var shape = evt.target;
    shape.moveTo(this.layer);
    this.stage.draw();
    shape.to({
      duration: 0.5,
      easing: Konva.Easings.ElasticEaseOut,
      scaleX: shape.getAttr('startScale'),
      scaleY: shape.getAttr('startScale'),
      shadowOffsetX: 5,
      shadowOffsetY: 5
    });
  });
  
  }

}



